@media only screen and (min-width: 414px) and (max-width: 767px) {
  .main {
    position: relative;
    top: 60px;
    margin: 0px 0px;
  }
}
@media only screen and (min-width: 768px) {
  .main {
    position: relative;
    top: 60px;
    margin: 0px 150px;
  }
}

.show {
  display: block;
}
.hide {
  display: none;
}
.topbarLayout {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: space-evenly;
}
.button {
  font-family: 'Roboto Mono', monospace;
  padding: 0px;
  height: 40px;
  width: 100px;
  margin: 10px 0px;
  color: var(--text-color);
  border: 1px solid var(--text-color);
  background-color: rgba(0, 255, 65, 0.5);
  border-radius: 4px;
}
.list {
  list-style-type: none;
  padding: 0px;
}
.card {
  height: 120px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas: 
      "info score"
      "body body"
      "body body"
      "body body"
      "source date";
  border: 1px solid var(--text-color);
  margin: 4px auto;
  font-size: 0.8em;
  border-radius: 4px;
  max-width: 1000px;
}
.info {
  grid-area: info;
  margin: 0px 8px;
}
.source {
  grid-area: source;
  margin: 0px 8px;
}
.score {
  grid-area: score;
  text-align: right;
  margin: 0px 8px;
}
.date {
  grid-area: date;
  text-align: right;
  margin: 0px 8px;
}
.text {
  grid-area: body;
  margin: 0px 8px;
  text-align: center;
  padding: 30px 0px;
}
.selected {
  background-color: rgba(0, 255, 65, 0.5);
}
.wrapText {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.multiWrap {
  padding: 10px 20px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}