@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400&display=swap);
html, body {
  --background-color: rgb(31, 31, 31);
  --text-color: rgb(223, 223, 223);
  margin: 0px;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color);
  background-color: var(--background-color);
}

@media only screen and (min-width: 414px) and (max-width: 767px) {
  .main {
    position: relative;
    top: 60px;
    margin: 0px 0px;
  }
}
@media only screen and (min-width: 768px) {
  .main {
    position: relative;
    top: 60px;
    margin: 0px 150px;
  }
}

.show {
  display: block;
}
.hide {
  display: none;
}
.topbarLayout {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: space-evenly;
}
.button {
  font-family: 'Roboto Mono', monospace;
  padding: 0px;
  height: 40px;
  width: 100px;
  margin: 10px 0px;
  color: var(--text-color);
  border: 1px solid var(--text-color);
  background-color: rgba(0, 255, 65, 0.5);
  border-radius: 4px;
}
.list {
  list-style-type: none;
  padding: 0px;
}
.card {
  height: 120px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas: 
      "info score"
      "body body"
      "body body"
      "body body"
      "source date";
  border: 1px solid var(--text-color);
  margin: 4px auto;
  font-size: 0.8em;
  border-radius: 4px;
  max-width: 1000px;
}
.info {
  grid-area: info;
  margin: 0px 8px;
}
.source {
  grid-area: source;
  margin: 0px 8px;
}
.score {
  grid-area: score;
  text-align: right;
  margin: 0px 8px;
}
.date {
  grid-area: date;
  text-align: right;
  margin: 0px 8px;
}
.text {
  grid-area: body;
  margin: 0px 8px;
  text-align: center;
  padding: 30px 0px;
}
.selected {
  background-color: rgba(0, 255, 65, 0.5);
}
.wrapText {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.multiWrap {
  padding: 10px 20px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.Loader_loader__3MXRD {
    --loader-unit: 16px;
    border: var(--loader-unit) solid rgb(63, 63, 63);
    border-top: var(--loader-unit) solid rgba(0, 255, 65, 0.5);
    border-radius: 50%;
    width: calc(4 * var(--loader-unit));
    height: calc(4 * var(--loader-unit));
    -webkit-animation: Loader_spin__35Z7G 2s linear infinite;
            animation: Loader_spin__35Z7G 2s linear infinite;
    position: absolute;
    top: calc(50% - 2 * var(--loader-unit));
    left: calc(50% - 2 * var(--loader-unit));
}
  
@-webkit-keyframes Loader_spin__35Z7G {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
  
@keyframes Loader_spin__35Z7G {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
.Topbar_component__1EqBD {
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    border-bottom: 1px solid var(--text-color);
    background-color: var(--background-color);
    z-index: 1;

}
